import { Link } from "react-router-dom";

import { ReactComponent as GoogleIcon } from "../../assets/images/icon/google.svg";
import { ReactComponent as FacebookIcon } from "../../assets/images/icon/fb.svg";

import styles from "./alternativeLogin.module.scss";

const AlternativeLogin = () => {
  return (
    <div>
      <p className={styles.or}>Або</p>
      <div className={styles.accountContainer}>
        <p>Увійти за допомогою:</p>
        <div className={styles.account}>
          <Link className={styles.link}>
            <GoogleIcon />
          </Link>
          <Link>
            <FacebookIcon />
          </Link>
        </div>
      </div>
    </div>
  );
};

export default AlternativeLogin;
