import { nanoid } from "nanoid";

import styles from "./input.module.scss";

const Input = ({
  placeholder = "Text",
  handleChange,
  type,
  name,
  required,
}) => {
  const id = nanoid();

  return (
    <input
      id={id}
      type={type}
      name={name}
      required={required}
      className={styles.input}
      placeholder={placeholder}
      onChange={handleChange}
    />
  );
};

export default Input;
